<template>
  <div class="container">
    <el-breadcrumb class="path" separator-class="el-icon-arrow-right">
      <span style="float:left">当前位置：</span>
      <el-breadcrumb-item>文章管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/article/insert' }">添加文章</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row>
      <el-col :lg="17" :md="17" :sm="24" :xs="24" class="form-left">
        <el-form :rules="rules" ref="dataForm" :model="Form" label-position="left" label-width="120px">
          <el-form-item label="文章标题：" prop="title" :inline-message="true">
              <el-input v-model="Form.title" placeholder="文章标题" style="width:50%"></el-input>
          </el-form-item>
          <el-form-item label="文章关键词：" prop="keywords" :inline-message="true">
              <el-input v-model="Form.keywords" placeholder="文章关键词" style="width:60%"></el-input>
          </el-form-item>
          <el-form-item label="文章描述：" prop="des" :inline-message="true">
              <el-input type="textarea" v-model="Form.des" placeholder="文章描述" style="width:70%"></el-input>
          </el-form-item>
          <el-form-item label="文章标签：" prop="des">
            <div class="tag-box">
              <el-checkbox-group
                v-model="Form.tag"
                >
                <el-checkbox v-for="(item, index) in tag" :label="item" :key="index">{{item}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </el-form-item>
          <el-form-item label="文章内容：">
            <div class="content-box">
              <mavon-editor v-model="Form.content"/>
            </div>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :lg="5" :md="5" :sm="24" :xs="24" class="form-right">
        <el-form :rules="rules" ref="dataForm" :model="Form" label-position="left" label-width="85px">
          <el-form-item label="文章来源：" prop="origin">
              <el-select v-model="Form.origin" placeholder="请选择">
                <el-option
                  v-for="item in origin"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
          </el-form-item>
          <el-form-item label="文章状态：" prop="origin">
            <el-switch
              v-model="Form.status"
              active-text="直接发布"
              inactive-text="存为草稿">
            </el-switch>
          </el-form-item>
          <el-form-item label="是否公开：" prop="origin">
            <el-switch
              v-model="Form.open"
              active-text="公开"
              inactive-text="私密">
            </el-switch>
          </el-form-item>
        </el-form>
        <div class="thumb_box">
          <p>缩略图：</p>
          <el-upload
            class="thumb-uploader"
            action="https://jsonplaceholder.typicode.com/posts/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="imageUrl" class="thumb">
            <i v-else class="el-icon-plus thumb-uploader-icon"></i>
          </el-upload>
        </div>
         <el-button type="primary" class="submit" icon="el-icon-arrow-up">发布文章</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tag: ['js', 'java', 'php'],
      origin: [
        {
          label: '原创',
          value: '0'
        },
        {
          label: '转载',
          value: '1'
        },
        {
          label: '混合',
          value: '2'
        }

      ],
      Form: {
        title: '',
        keywords: '',
        des: '',
        tag: [],
        content: '',
        origin: '0',
        status: true,
        open: true
      },
      rules: {
        title: [{ required: true, message: '请输入文章标题', trigger: 'blur' }],
        keywords: [{ required: true, message: '请输入文章关键词', trigger: 'blur' }],
        des: [{ required: true, message: '请输入文章描述', trigger: 'blur' }]
      },
      imageUrl: ''
    }
  },
  methods: {
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    }
  }
}
</script>

<style lang="stylus" scoped>
.container
  .form-left
    padding 10px 20px
    box-sizing border-box
    margin 0 15px
    background-color #fff
    .el-form-item
      margin-bottom 10px
    .tag-box
      width 50%
      padding 0 10px
      min-height 60px
      height 100%
      border-radius 5px
      border 1px solid #ddd
    .content-box
      width 98%
    .v-note-wrapper
      max-height 650px
  .form-right
    padding 10px 20px
    box-sizing border-box
    margin 0 10px
    background-color #fff
    .thumb_box
      text-align center
      p
        text-align left
        font-size 15px
        color #606266
        line-height 40px
        padding 0 12px 0 0
      .thumb-uploader
        width 210px
        height 150px
        margin 0 auto
        border 1px dashed #d9d9d9
        border-radius 6px
        cursor pointer
        position relative
        overflow hidden
        .el-upload
          &:hover
            background #fff
        .thumb-uploader-icon
          font-size 28px
          color #8c939d
          width 210px
          height 150px
          line-height 150px
          text-align center
        .thumb
          width 210px
          height 150px
          display block
    .submit
      display block
      width 80%
      margin 30px auto 20px auto
</style>



